.login-page-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: left;
  margin: 50px;
}

.login-title {
  margin-bottom: 10px;
  text-align: center;
}

.login-message {
  margin-bottom: 10px;
}

.login-label {
  width: 300px;
  margin-top: 30px;
}

.login-input {
  width: 280px;
  padding: 10px;
}

.error-message {
  width: 300px;
  color: red;
  font-size: 0.8em;
}

.login-btn2 {
  font-size: 15px;
  padding: 10px;
  width: 300px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  margin-top: 30px;
  margin-bottom: 30px;
  font-weight: bold;
}

.login-btn2:disabled {
  background-color: #007bff; /* 비활성화된 상태일 때의 배경색 */
  opacity: 50%;
  cursor: not-allowed;
}

.login-links {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.login-links a {
  text-decoration: none;
  color: black;
}

.separator {
  margin: 0 8px;
}
